<template>
    <div class="notice">
        <Banner home title="相关通知"></Banner>
        <ul id="notices">
            <li class="actived">紧急通知</li>
            <li>一般通知</li>
        </ul>
        <Operation ref="nOperate"></Operation>
    </div>
</template>
<script>
import Banner from './items/Banner'
import Operation from './items/Operation'
export default {
    data(){
        return {
            flag: 0
        }
    },
    components: {
        Banner,
        Operation
    },
    methods: {
        handleNotice(ref) {
            let oUl = document.getElementById('notices');
            if(oUl){
                oUl.addEventListener('click', function(ev){
                    let e = ev || window.event;
                    let target = e.target || e.srcElement;
                    if(target && target.nodeName=='LI'){
                        console.log(this);
                        let child = this.children;
                        for(let i=0;i<child.length;i++){
                            if(child[i]==target){
                                child[i].className = 'actived';
                                this.flag = i;
                                ref['toggleData'](i);
                            }else{
                                child[i].className = '';
                            }
                        }
                    }
                })
            }
        }
    },
    beforeCreate() {
        console.log('Notice.vue beforeCreate');
    },
    created() {
        console.log('Notice.vue Created');
    },
    beforeMount() {
        console.log('Notice.vue beforeMount');
    },
    mounted() {
        console.log('Notice.vue mounted');
        console.log(this.$route);
        this.handleNotice(this.$refs.nOperate);
    },
    beforeUpdate() {
        console.log('Notice.vue beforeUpdate');
    },
    updated() {
        console.log('Notice.vue updated');
    },
    beforeDestroy() {
        console.log('Notice.vue beforeDestroy');
    },
    destroyed() {
        console.log('Notice.vue destroyed');
    },
}
</script>
